@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Nunito", sans-serif;
  text-transform: capitalize;
}

:root {
  --bgcolor: #f1f5fd;
  --primary-color: #6f5de7;
  --text-coloe: #8383a3;
  --main-text: #333333;
}

html {
  font-size: 62.5%;
}
html::-webkit-scrollbar {
  width: 1rem;
}

html::-webkit-scrollbar-track {
  background-color: #eee;
}

html::-webkit-scrollbar-thumb {
  background-color: orange;
}

a {
  text-decoration: none;
  color: #fff;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--main-text);
}

.navbar-bg {
  background-color: var(--bgcolor);
  z-index: 100;
  padding: 2rem 0;
  position: relative;
}

.navbar-brand {
  font-size: 2.5rem;

  font-weight: bold;
}

.navbar-brand:hover {
  letter-spacing: 2px;
}

.navbar-brand span {
  color: orange;
}

.navbar li {
  padding: 0 2rem;
}

.navbar li a {
  font-size: 1.7rem;
  font-weight: lighter;
}

.navbar li a:hover {
  color: orange !important;
  border-bottom: 4px solid #333;
}

.btn-style {
  width: 12rem;
  height: 4rem;
  font-size: 1.6rem;
  border-radius: 2rem;
  font-weight: bold;
  border: 0.1rem solid #333;
}

.btn-style:hover {
  background: white;
  color: orange;
  border: none;
}

.btn-style:first-child {
  margin-right: 2rem;
}

.header-left-side {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
}

.header-right-side {
  display: flex;
  justify-content: center;

  align-items: center;
}

header {
  background-color: var(--bgcolor);
  height: 100vh;
  width: 100%;
}

.main-hero-container {
  height: 80vh;
  display: flex;
}
.main-hero-container h1 {
  font-weight: bolder;
}
.main-hero-container h1 span {
  color: orange;
}

.main-hero-para {
  font-size: 1.5rem;
  color: var(--main-text);
  margin: 3rem 0 5rem 0;
  font-weight: lighter;
  width: 75%;
  text-transform: none;
  font-weight: 500;
}

.input-group {
  position: relative;
}
.form-control-text {
  position: absolute;
  height: 5rem;
  border: none;
  outline: none;
  padding-left: 1.5rem !important;
  font-size: 1.4rem;
}

.input-group-button {
  position: absolute;
  left: 60%;
  top: 0.5rem;
  color: white;
  font-weight: bold;
  background-color: orange;
  height: 4rem;
  width: 10rem;
  border-radius: 1.5rem !important;
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.input-group-button:hover {
  border: 1px solid orange;
  background-color: white;
  color: orange;
  letter-spacing: 0.2rem;
  cursor: pointer;
}

.head-img {
  position: relative;
}

.head-img img {
  width: 70%;
  height: 75%;

  box-shadow: 0 0.8rem 0.8rem rgba(0, 0, 0, 0.25);
  border-radius: 1.5rem;
  position: absolute;
}

.head-img .head-img2 {
  position: absolute;
  left: 55%;
  bottom: -3%;
  width: 50%;
  height: 40%;
}

/*----------------------------------------------- Work section start
                         -----------------------------------------------*/

.work-comtainer {
  width: 80vw;
  padding: 5rem;
  box-shadow: 0 0.4rem 1.2rem rgba(0, 0, 0, 0.1);
  margin-top: -10rem;
  border-radius: 0.5rem;
  background-color: white;
}

.work-comtainer .main-heading {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 4rem;
  letter-spacing: 0.5rem;
  color: orange;
}

.work-subcontainere {
  padding: 0 6rem;
}

.work-comtainer .sub-headind {
  font-size: 2rem;
}

.fontawosome-style {
  font-size: 2.3rem;
  height: 6rem;
  width: 6rem;
  background-color: var(--bgcolor);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 2rem auto;
}

.fontawosome-style:hover {
  color: orange;
}

/*----------------------------------------------- Work section end
                         -----------------------------------------------*/

/*----------------------------------------------- service section start
                         -----------------------------------------------*/

.our-services img {
  width: 50%;
  height: 80%;
  margin-top: 10rem;
  object-fit: cover;
  border-radius: 1.5rem;

  box-shadow: 0 0.8rem 0.8rem rgba(0, 0, 0, 0.25);
}

.our-services .mini-title {
  text-transform: uppercase;
  font-weight: normal;
  margin: 3rem 0;
  text-align: left;
  font-size: 1.5rem;
}

.our-services .main-heading {
  font-weight: bolder;
}

.our-services-info {
  margin-top: 4rem;
}

.our-services .our-services-num {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  border: 0.1rem solid orange;
  color: orange;
  display: grid;
  font-weight: bolder;
  place-items: center;
  margin-right: 2rem;
}
.our-services .our-services-data p {
  margin: 1rem 0;
}

.our-services .btn-style:hover {
  color: white;
  background-color: orange;
}
.about-img {
  float: left;
  width: 100%;
  height: 80%;
  margin-top: 10rem;
  object-fit: fill;
  border-radius: 1.5rem;
  margin-right: 10rem;
  box-shadow: 0 0.8rem 0.8rem rgba(0, 0, 0, 0.25);
}
.our-services-list-right {
  margin-left: 10rem;
}

.submit-btn {
  background-color: orange;
  color: white;
  font-size: 20px;
  border-radius: 10rem;
}

.submit-btn:hover {
  background-color: rgb(10, 184, 241);
  color: white;
  font-weight: bold;
}

.contactus-section {
  margin: 15rem 0;
}

.contact-leftside h1 span {
  color: orange;
}

.contact-leftside img {
  min-width: 80%;
  max-height: 30rem;
  height: auto;
}

.contact-input-field {
  margin-bottom: 4rem;
}

.form-control {
  border: 1px solid #ced4da;
  -webkit-appearance: none;
  height: 4rem;
  border-radius: 0.5rem;
  font-size: 1.5rem;
  padding-left: 1rem;
  font-weight: lighter;
}

.form-check-input {
  width: 1.5rem;
  height: 1.5rem;
}

.form-chck-boxstyle .main-hero-para {
  width: 100%;
  margin: 0;
  margin-bottom: 4rem;
  padding: 0 2rem;
  color: #666;
}
/*----------------------------------------------- service section start
                         -----------------------------------------------*/
.services-main-container {
  margin-top: 5rem;
  padding: 5rem 0;
}

.services-main-container .main-heading {
  margin: 3rem 0;
}

.services-main-container .work-subcontainere {
  box-shadow: 0 0.8rem 0.8rem rgba(68, 47, 47, 0.1);
  border-radius: 1.5rem;
}

.services-main-container .work-subcontainere:hover {
  background-color: var(--bgcolor);
  transform: scale(1.05, 1.05);
}
/*----------------------------------------------- service section end
                         -----------------------------------------------*/

/*----------------------------------------------- footer section start
                         -----------------------------------------------*/

footer {
  background-color: #182334;
  color: white;
  margin-top: 5rem;
}

.contact-info {
  margin-top: 2rem;
}

.contact-info h2 {
  color: orange;
  font-size: 2rem;
}

.contact-info ul li {
  text-decoration: none;
  font-size: 1.5rem;
}

.contact-info a i {
  background: none;
  color: white;
}
footer .main-hero-para {
  color: white;
  font-weight: lighter;
}

footer .fontawosome-style:hover {
  transition: all 1s;
  animation: rotate 1s linear infinite alternate-reverse;
}

@keyframes rotate {
  0% {
    transform: scale(0.6);
  }
  100% {
    transform: scale(1.2);
  }
}

/*----------------------------------------------- footer section end
                         -----------------------------------------------*/

/*Media Queries start*/

@media (min-width: 1500px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl {
    max-width: 1500px !important;
  }
}

@media (min-width: 1400px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl {
    max-width: 1400px !important;
  }
}
